body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#app, #advertising {
  color: #fff;
  width: 1200px;
  height: 800px;
}

#advertising {
  position: relative;
}

#advertising-title {
  font-optical-sizing: auto;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 0 5px #000, 0 0 10px #000, 0 0 15px #000;
  width: 100%;
  padding: 25px 0;
  font-family: Lexend, sans-serif;
  font-size: 2em;
  font-style: normal;
  font-weight: 700;
  position: absolute;
  top: 50px;
  color: #e10bff !important;
}

#advertising-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 100%;
}

#advertising-avatar {
  z-index: 99;
  align-items: center;
  width: 100%;
  height: 300px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}

#advertising-avatar-img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 15px;
  width: 300px;
  height: 300px;
  margin-right: 50px;
  position: relative;
}

#advertising-avatar-name {
  text-align: center;
  color: #fff;
  text-shadow: 0 0 5px #000, 0 0 10px #000, 0 0 15px #000;
  font-family: Permanent Marker, cursive;
  font-size: 1.5em;
  font-style: normal;
  font-weight: 400;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}

#advertising-avatar-text {
  text-align: center;
  flex: 2;
  font-family: Permanent Marker, cursive;
  font-size: 4em;
  font-style: normal;
  font-weight: 400;
}
